<template>
  <div class="pageContent">
    <!-- 筛选条件 -->
    <el-card class="filter-container" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right" @click="getData" type="primary" size="small">查询</el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small">重置</el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="商品名称：">
            <el-input style="width: 203px" v-model="searchList.name" clearable placeholder="商品名称" @input="change($event)"></el-input>
          </el-form-item>
          <el-form-item label="商品类目：">
            <el-cascader
              v-model="searchList.shopCategoryIdArr"
              :options="category"
              :props="{ checkStrictly: true }"
              clearable></el-cascader>
          </el-form-item>
          <el-form-item label="商品编码：">
            <el-input style="width: 203px" v-model="searchList.code" clearable placeholder="商品编码" @input="change($event)"></el-input>
          </el-form-item>
          <el-form-item label="规格编码：">
            <el-input style="width: 203px" v-model="searchList.skuCode" clearable placeholder="规格编码" @input="change($event)"></el-input>
          </el-form-item>
          <el-form-item label="物流服务：">
            <el-select v-model="searchList.companyType" clearable placeholder="请选择物流服务">
              <el-option label="包邮" value="0"></el-option>
              <el-option label="不包邮" value="1"></el-option>
              <el-option label="到店自提" value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="库存预警：">
            <el-select v-model="searchList.productWarnType" clearable placeholder="请选择库存状态">
              <el-option label="已售罄（当前库存为0）" value="0"></el-option>
              <el-option label="已滞销（销量为0）" value="1"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="会员价区间：">
            <el-input-number style="width:100px" v-model="searchList.minVipPrice" :precision="2" :controls="false" @input="change"></el-input-number> &nbsp;-&nbsp;
            <el-input-number style="width:100px" v-model="searchList.maxVipPrice" :precision="2" :controls="false" @input="change"></el-input-number>
          </el-form-item>
          <el-form-item label="销售价区间：">
            <el-input-number style="width:100px" v-model="searchList.minPrice" :precision="2" :controls="false" @input="change"></el-input-number> &nbsp;-&nbsp;
            <el-input-number style="width:100px" v-model="searchList.maxPrice" :precision="2" :controls="false" @input="change"></el-input-number>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 - head-->
    <el-card class="operate-container mt10" shadow="never">
      <div class="flex-row-between" style="width:100%;">
        <div>
          <i class="el-icon-tickets"></i>
          <span>数据列表</span>
          <span style="margin-left:40px;color:#E6A23C;">温馨提示：若会员商品同时参与了限时折扣、砍价等营销活动.默认商品以活动商品的形式展示</span>
        </div>
        <div style="padding-right:20px;">
          <el-button type="text" icon="el-icon-circle-plus-outline" @click="commodityShow = true">新增会员商品</el-button>
        </div>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <el-card style="margin-top:20px;" class="filter-container" shadow="never">
      <el-table
        ref="multipleTable"
        :data="list"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column label="商品信息" width="400">
          <template slot-scope="scope">
            <div class="flex-row-start">
              <el-image style="width: 100px; height: 100px" :src="scope.row.imageUrl" fit="fill"></el-image>
              <span style="flex:1;margin-left:10px;">{{ scope.row.name }}</span>
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="productCategoryShopName" label="分类" width="100"></el-table-column>
        <el-table-column prop="sumStock" label="库存"></el-table-column>
        <el-table-column label="成本价" width="200">
          <template slot-scope="scope">
            <div>{{scope.row.originPrice ? scope.row.originPrice : '-'}}</div>
          </template>
        </el-table-column>
        <el-table-column prop="price" label="销售价" width="200"></el-table-column>
        <el-table-column label="会员折扣" width="200">
          <template slot-scope="scope">
            <div class="flex-row-center">
              <span>{{scope.row.vipRate}}</span>
              <el-popover placement="top" width="240" trigger="click" v-model="scope.row.zkShow">
                <el-input-number 
                  v-model="scope.row.zk" 
                  step-strictly 
                  :controls="false"
                  :min="1"
                  :max="100"></el-input-number>
                <el-button type="text" @click="editItem(scope.row)">确定</el-button>
                <i slot="reference" class="el-icon-edit"></i>
              </el-popover>
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="vipPrice" label="会员价" width="200"></el-table-column>
        <el-table-column label="商品状态" width="100">
          <template slot-scope="scope">
            <div class="succeseFont" v-if="scope.row.salesStatus == 0">出售中</div>
            <div classs="dangerFont" v-if="scope.row.salesStatus == 1">下架</div>
            <div class="warningFont" v-if="scope.row.salesStatus == 2">待出售</div>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="160">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="$router.push({path:'/commodity/commodityList/commodityInfo',query:{productId:scope.row.productId}})">查看详情</el-button>
            <el-button style="color:#909399;" type="text" size="small" @click="deleteItem(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="flex-row-between" style="padding-top:20px;">
        <div class="flex-row-start">
          <div class="flex-row-start">
            <el-checkbox @change="selectAllSureList" v-model="selectAllSure">当前页全选</el-checkbox>
            <div class="checkBoxDiv">
              已选择 <span style="color:#409EFF">{{multipleSelection.length}}</span> 项
            </div>
          </div>
          <div class="flex-row-start" style="margin-left:20px">
            <el-button style="margin-right:10px;" @click="deleteAll">批量删除</el-button>
            <el-popover placement="top" width="240" trigger="click" v-model="zkShow">
              <el-input-number 
                v-model="zk" 
                :step="2" 
                step-strictly 
                :controls="false" 
                :min="1"
                :max="100"></el-input-number>
              <el-button type="text" @click="editAll">确定</el-button>
              <el-button slot="reference" type="primary">批量设置折扣</el-button>
            </el-popover>
            
          </div>
        </div>
        <div>
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :page-sizes="[5,10,15,20]"
            :page-size="searchList.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="listTotal">
          </el-pagination>
        </div>
      </div>
    </el-card>
    <vipList :commodityShow="commodityShow" @commodityClose="commodityClose" @commodityFun="commodityFun"></vipList>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { getProductCategoryShopAllList } from '@/api/commodity/commodityClassify'
import { getVipProductInfoList , saveVipProduct , deleteVipProduct , editVipProduct } from '@/api/memberManage/memberProduct'
import vipList from './components/vipList';
export default {
  data(){
    return{
      zk:undefined,                          // 批量设置折扣
      zkShow:false,
      searchList:{
        tabType:'1',
        shopCategoryIdArr:[],
        pageNum:1,
        pageSize:5,
        productWarnType:''
      },
      category:[],
      list:[],
      listTotal:0,
      multipleSelection:[],           // 勾选的数据
      selectAllSure:false,            // 列表全选
      props: { multiple: true },
      commodityShow:false,            // 父级确定
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
    this.getData();
    this.getCategory()
  },
  methods:{
    commodityClose(){
      this.commodityShow = false;
    },
    commodityFun(e){
      console.log('父级确定')
      this.commodityShow = false;
      let arr = []
      saveVipProduct(e).then(res=>{
        console.log('保存会员商品',res)
        if(res.code == 200){
          this.$message({
            message: '添加成功',
            type: 'success'
          });
          this.getData()
        }
      })
    },
    deleteItem(e){
      this.$confirm('确定将此商品移除会员商品池？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        deleteVipProduct([{id:e.id}]).then(res=>{
          console.log('删除会员商品',res)
          if(res.code == 200){
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
            this.getData()
          }else{
            this.$message.error(res.message);
          }
        })
        
      }).catch(() => {});
    },
    deleteAll(){
      console.log('this.multipleSelection',this.multipleSelection)
      if(this.multipleSelection.length == 0){
        this.$message.error('请选择商品后再试');
        return;
      }
      this.$confirm('确定将商品移除会员商品池？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        deleteVipProduct(this.multipleSelection).then(res=>{
          console.log('删除会员商品',res)
          if(res.code == 200){
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
            this.getData()
          }else{
            this.$message.error(res.message);
          }
        })
      }).catch(() => {});
    },
    editItem(e){
      console.log(e)
      if(!e.zk){
        this.$message.error('请输入折扣');
        return;
      }
      editVipProduct([{id:e.id,vipRate:e.zk}]).then(res=>{
        console.log('设置会员折扣',res)
        if(res.code == 200){
          this.$message({
            type: 'success',
            message: '设置成功!'
          });
          this.getData()
        }else{
          this.$message.error(res.message);
        }
      })
    },
    editAll(){
      if(!this.zk){
        this.$message.error('请输入折扣');
        return;
      }
      if(this.multipleSelection.length == 0){
        this.$message.error('请选择商品后再试');
        return;
      }
      this.multipleSelection.forEach(e=>e.vipRate = this.zk)
      editVipProduct(this.multipleSelection).then(res=>{
        console.log('设置会员折扣',res)
        if(res.code == 200){
          this.$message({
            type: 'success',
            message: '设置成功!'
          });
          this.getData()
        }else{
          this.$message.error(res.message);
        }
      })
    },
    // 列表 - 获取数据
    getData(){
      this.searchList.shopCategoryId = this.searchList.shopCategoryIdArr[1] || ''
      this.searchList.shopCategoryPid = this.searchList.shopCategoryIdArr[0] || ''
      console.log('请求的数据',this.searchList)
      getVipProductInfoList(this.searchList).then(res=>{
        console.log('会员商品池列表',res)
        if(res.code == 200){
          res.data.list.forEach(e => {
            e.zk = undefined
            e.zkShow = false
          })
          this.list = res.data.list
          this.listTotal = res.data.total
        }
      })
    },
    // 底部固定 - 全选
    selectAllSureList(){
      if (this.selectAllSure) {
        this.selectAllSure = false;
        this.$refs.multipleTable.toggleAllSelection();
      } else {
        this.selectAllSure = true;
        this.$refs.multipleTable.clearSelection();
      }
      console.log('multipleSelection',this.multipleSelection)
    },
    // 搜索栏 - 重置
    reset(){
      this.searchList.name = ''
      this.searchList.shopCategoryIdArr = []
      this.searchList.shopCategoryId = ''
      this.searchList.shopCategoryPid = ''
      this.searchList.code = ''
      this.searchList.skuCode = ''
      this.searchList.companyType = ''
      this.searchList.productWarnType = ''
      this.searchList.minVipPrice = ''
      this.searchList.maxVipPrice = ''
      this.searchList.minPrice = ''
      this.searchList.maxPrice = ''
      this.getData()
    },
    // 表单选择
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
      this.selectAllSure = (this.multipleSelection.length == this.list.length) && (this.list.length != 0)
    },
    // 获取全部分类
    getCategory(){
      getProductCategoryShopAllList(this.shop.shopId).then(res=>{
        console.log('获取店铺分类',res)
        this.category = res.data
      })
    },
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.searchList.pageSize = val
      this.getData();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.searchList.pageNum = val
      this.getData();
    },
    change(){
      this.$forceUpdate()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
  components:{
    vipList
  }
}
</script>
<style lang="less" scoped>
  .checkBoxDiv{
    font-size: 14px;
    margin-left: 40px;
    line-height: 19px;
    color: #606266;
  }
</style>